@-webkit-keyframes fade {
  from {
    opacity: 0;
    -webkit-transform: scale3D(0.95, 0.95, 0.95);
            transform: scale3D(0.95, 0.95, 0.95);
  }
  to {
    opacity: 1;
    -webkit-transform: scale3D(1, 1, 1);
            transform: scale3D(1, 1, 1);
  }
}

@keyframes fade {
  from {
    opacity: 0;
