:local(.navbarBackground) {
  /* margin: 1rem auto; */
  margin: auto;
  position: relative;
  -o-transition: 1.5s ease;
  transition: 1.5s ease;
  -webkit-transition: 1.5s ease;
}
:local(.navbarBackground):hover {
  -o-transition: 1.5s ease;
  transition: 1.5s ease;
  -webkit-transition: 1.5s ease;
  font-size: 1.2rem;
}
Nav {
  font-size: 1.1rem;
}
